import {  
    mapMutations  
} from 'vuex';

export default{
	data(){
		return {
			nodes:''
		}
	},
	onLoad(e) {
		this.getpage(e.id);
	},
	methods: {
		...mapMutations(['zhecexieyi']),
		jujue(){
			uni.navigateBack({
				delta:1
			})
		},
		tongyi(){
			uni.redirectTo({
				url:"register"
			})
		},
		getpage(id){
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/page/onepage',
				data:{id:id},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					var one = resa.data;
					this.nodes = one.one.content;
					uni.setNavigationBarTitle({
						title: one.one.title
					});
				}
			});
		}
	}
}
